.box{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(222, 152, 47);
    margin: 5px;
}
.box-small{
    width: 50px;
    height:50px;    
}

.box-medium{
    width: 100px;
    height: 100px;
}

.box-big{
    width: 150px;
    height: 150px;
}
